import cx from 'classnames';
import { FunctionComponent } from "react";
import { NumericFormat } from 'react-number-format';


interface InputProps {
    name?: string;
    value?: number;
    onChange: (value?: number) => void;
    prefix?: string;
    suffix?: string;
}

interface NumberInputWithLabelProps extends InputProps {
    label: string;
}


export const NumberInput: FunctionComponent<InputProps> = ({
    name,
    value,
    onChange,
    prefix,
    suffix
}) => {
    name = name ?? `${Math.random()}}`;
    return <div className="relative mt-1 rounded-md shadow-sm h-12">
        {
            !!prefix && <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3 h-full">
                <span className="text-gray-500 text-base">
                    {prefix}
                </span>
            </div>
        }
        <NumericFormat
            allowLeadingZeros={false}
            thousandSeparator=","
            name={name} id={name}
            value={value}
            onValueChange={e => {
                onChange(e.floatValue);
            }}
            className={cx({
                "block w-full h-full rounded-md border-0 py-1.5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary text-base": true,
                "pr-7": suffix && suffix.length < 2,
                "pr-10": suffix && suffix.length == 2,
                "pr-11": suffix && suffix.length == 3,
                "pr-12": suffix && suffix.length == 4,
                "pr-13": suffix && suffix.length == 5,
                "pr-14": suffix && suffix.length >= 6,
                "pl-7": prefix && prefix.length < 2,
                "pl-10": prefix && prefix.length == 2,
                "pl-11": prefix && prefix.length == 3,
                "pl-12": prefix && prefix.length == 4,
                "pl-13": prefix && prefix.length == 5,
                "pl-14": prefix && prefix.length >= 6,

                "pl-4": !prefix,
                "pr-4": !suffix
            })} placeholder="0.00"
            min={0}
        />
        {
            !!suffix && <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3  h-full">
                <span className="text-gray-500 text-base">
                    {suffix}
                </span>
            </div>
        }
    </div>
}


const NumberInputWithLabel: FunctionComponent<NumberInputWithLabelProps> = ({
    name,
    label,
    ...rest
}) => {
    name = name ?? `${label.toLowerCase()}-${Math.random()}}`;
    return <div className="form-control">
        <label className="label label-text font-bold pl-0" htmlFor={name}>
            {label}
        </label>
        <NumberInput name={name} {...rest} />
    </div>
}

export default NumberInputWithLabel;