import { MessageAction } from "./message-actions";
const source = "__sales_discount_calculator__";
export type ExtensionMessagePayload = {
  action: MessageAction;
  payload?: any;
  source: typeof source;
  version: string;
};
export default function prepareMessage(
  action: MessageAction,
  payload?: any
): ExtensionMessagePayload {
  const version = chrome.runtime.getManifest().version;
  return { source, action, payload, version };
}

export function isValidMessage(message: any) {
  const version = chrome.runtime.getManifest().version;
  return message?.source === source && message?.version === version;
}
