/**
 * Format a number with commas
 * Input: 1000000 Output: 1,000,000
 * Input: 1000000.123 Output: 1,000,000.123
 * Input: 1000000.123456 Output: 1,000,000.123456
 * @param number 
 * @returns 
 */
export default function formatNumber(number: number | undefined, precision = 20): string {
    if (typeof number !== 'number') {
        number = 0;
    }
    return new Intl.NumberFormat('en-US', { minimumFractionDigits: 2, maximumFractionDigits: precision }).format(number);
}