import cx from 'classnames';
import { useEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import CloseIcon from './components/closeIcon';
import MoveHorizontalIcon from './components/moveHorizontalIcon';
import MoveVerticalIcon from './components/moveVerticalIcon';
import PercentageCalculator from './components/percentage-calculator';
import PriceCalculator from './components/price-calculator';
import { MessageAction } from './message-actions';
import prepareMessage from './prepare-message';
const container = document.getElementById('root')!;
enum CalculatorMode {
    PriceCalculator,
    PercentageCalculator
}

const App = () => {
    const [mode, setMode] = useState(CalculatorMode.PriceCalculator);
    const [standalone, setStandalone] = useState(false);
    useEffect(() => {
        const focusOnFirstInput = () => {
            const input = document.querySelector('input') as HTMLInputElement;
            input?.focus();
        }
        const broadcastHeight = () => {
            const menuHeight = document.getElementById('calculator-menu')?.getBoundingClientRect().height ?? 0;
            const screenHeight = document.getElementById('calculator-screen')?.getBoundingClientRect().height ?? 0;
            const height = menuHeight + screenHeight;
            const msg = prepareMessage(MessageAction.SET_MAX_HEIGHT, height);
            console.log('broadcasting height', height);
            chrome.runtime.sendMessage(msg);
        }
        broadcastHeight();
        const resizeObserver = new ResizeObserver(broadcastHeight);
        resizeObserver.observe(
            document.getElementById('calculator-menu')!,
        );
        resizeObserver.observe(
            document.getElementById('calculator-screen')!,
        );
        focusOnFirstInput();
        return () => {
            resizeObserver.disconnect();
        }
    }, [mode]);
    const closeModal = () => {
        const msg = prepareMessage(MessageAction.HIDE_POPUP);
        chrome.runtime.sendMessage(msg);
    };
    const closeWindow = () => {
        window.close();
    }
    const moveVertical = () => {
        const msg = prepareMessage(MessageAction.TOGGLE_VERTICAL_POSITION);
        chrome.runtime.sendMessage(msg);
    };
    const moveHorizontal = () => {
        const msg = prepareMessage(MessageAction.TOGGLE_HORIZONTAL_POSITION);
        chrome.runtime.sendMessage(msg);
    };
    useEffect(() => {
        const searchQuery = window.location.search;
        const params = new URLSearchParams(searchQuery);
        const standalone = params.get('standalone') === 'true';
        setStandalone(standalone);
        if (standalone) {
            document.body.classList.add('standalone');
        } else {
            document.body.classList.remove('standalone');
        }
    }, [])
    return (
        <div className="container">
            <div
                className={cx('flex flex-col relative', !standalone && 'h-screen', {
                    'rounded-xl': standalone,
                    'shadow-lg': standalone,
                    'overflow-hidden': standalone,
                })}
            >
                <ul className="menu menu-horizontal bg-base-100 w-full flex" id="calculator-menu">
                    <li><a
                        className={
                            cx('text-base', {
                                active: mode === CalculatorMode.PriceCalculator
                            })
                        }
                        onClick={() => setMode(CalculatorMode.PriceCalculator)}
                        href={`#${CalculatorMode.PriceCalculator}`}
                    >
                        Price
                    </a></li>
                    <li><a
                        className={
                            cx('text-base', {
                                active: mode === CalculatorMode.PercentageCalculator
                            })
                        }
                        onClick={() => setMode(CalculatorMode.PercentageCalculator)}
                        href={`#${CalculatorMode.PercentageCalculator}`}
                    >
                        Percentage
                    </a></li>
                    <li className="flex-1 invisible"></li>
                    {
                        !standalone && <>
                            <li>
                                <a className='text-lg'
                                    onClick={moveVertical}
                                    title='Move Vertical'
                                >
                                    <MoveVerticalIcon stroke='currentColor' />
                                </a>
                            </li>
                            <li>
                                <a className='text-lg'
                                    onClick={moveHorizontal}
                                    title='Move Horizontal'
                                >
                                    <MoveHorizontalIcon stroke='currentColor' />
                                </a>
                            </li>
                        </>
                    }
                    <li>
                        <a className='text-lg'
                            onClick={
                                standalone ? closeWindow : closeModal
                            }
                            title='Close'
                        >
                            <CloseIcon stroke='currentColor' />
                        </a>
                    </li>
                </ul>
                <div id="calculator-screen">
                    {
                        mode === CalculatorMode.PriceCalculator && <PriceCalculator />
                    }
                    {
                        mode === CalculatorMode.PercentageCalculator && <PercentageCalculator />
                    }
                </div>
            </div>
        </div>
    );
}


const root = createRoot(container);

root.render(<App />);

