import { FunctionComponent, useMemo, useState } from "react";
import formatNumber from "../lib/format-number";
import CopyBtn from "./copy-btn";
import { NumberInput } from "./number-input";

interface PercentageCalculatorProps {

}


const Calculator1: FunctionComponent<{}> = () => {
    const [amount, setAmount] = useState<number>();
    const [percentage, setPercentage] = useState<number>();
    const result = useMemo(() => {
        const _amount = typeof amount === 'number' ? amount : 0;
        const _percentage = typeof percentage === 'number' ? percentage / 100 : 0;
        return _amount * _percentage;
    }, [amount, percentage]);
    const copyText = useMemo(() => {
        return [
            `Amount: $${formatNumber(amount)}`,
            `Percentage: ${formatNumber(percentage)}%`,
            `Result: ${formatNumber(result)}`].join('\n');
    }, [amount, percentage, result]);
    return <>
        <div className="flex flex-row gap-2">
            <div className="flex-1">
                <NumberInput value={percentage} onChange={setPercentage} suffix="%" />
            </div>
            <span
                className="text-1xl text-base text-gray-800 font-bold flex items-center"
            >
                of
            </span>
            <div className="flex-2">
                <NumberInput value={amount} onChange={setAmount} />
            </div>
        </div>
        <div className="flex flex-row justify-end gap-1">
            <span className="text-1xl text-base text-gray-800 font-bold">
                {formatNumber(result)}
            </span>
            <CopyBtn text={copyText} />
        </div>
    </>
}

const Calculator2: FunctionComponent<{}> = () => {
    const [amount1, setAmount1] = useState<number>();
    const [amount2, setAmount2] = useState<number>();
    const pt = useMemo(() => {
        const _amount1 = typeof amount1 === 'number' ? amount1 : 0;
        const _amount2 = typeof amount2 === 'number' ? amount2 : 0;
        if (_amount2 === 0) return 0;
        return _amount1 / _amount2 * 100;
    }, [amount1, amount2]);

    const copyText = useMemo(() => {
        return [
            `Amount 1: ${formatNumber(amount1)}`,
            `Amount 2: ${formatNumber(amount2)}`,
            `Percentage: ${formatNumber(pt)}%`].join('\n');

    }, [amount1, amount2, pt]);

    return <>
        <div className="flex flex-row gap-4">
            <div className="flex-1">
                <NumberInput value={amount1} onChange={setAmount1} />
            </div>
            <span
                className="text-1xl text-base text-gray-800 font-bold flex items-center"
            >
                as a % of
            </span>
            <div className="flex-1">
                <NumberInput value={amount2} onChange={setAmount2} />
            </div>
        </div>
        <div className="flex flex-row justify-end gap-1">
            <span className="text-1xl text-base text-gray-800 font-bold">
                {formatNumber(pt)}%
            </span>
            <CopyBtn text={copyText} />
        </div>
    </>
}

const Calculator3: FunctionComponent<{}> = () => {
    const [from, setFrom] = useState<number>();
    const [to, setTo] = useState<number>();
    const change = useMemo(() => {
        const _from = typeof from === 'number' ? from : 0;
        const _to = typeof to === 'number' ? to : 0;
        if (_from === 0) return 0;
        return (_to - _from) / _from * 100;
    }, [from, to]);

    const increaseDecrease = useMemo(() => {
        return change >= 0 ? 'increase' : 'decrease';
    }, [change]);

    const copyText = useMemo(() => {
        return [
            `From: ${formatNumber(from)}`,
            `To: ${formatNumber(to)}`,
            `Change: ${formatNumber(change)}% ${increaseDecrease}`].join('\n');

    }, [from, to, change, increaseDecrease]);

    return <>
        <div className="flex flex-row gap-4">
            <span
                className="text-1xl text-base text-gray-800 font-bold flex items-center"
            >
                from
            </span>
            <div className="flex-1">
                <NumberInput value={from} onChange={setFrom} />
            </div>
            <span
                className="text-1xl text-base text-gray-800 font-bold flex items-center"
            >
                to
            </span>
            <div className="flex-1">
                <NumberInput value={to} onChange={setTo} />
            </div>
        </div>
        <div className="flex flex-row justify-end gap-1">
            <span className="text-1xl text-base text-gray-800 font-bold">
                {formatNumber(change)}% {increaseDecrease}
            </span>
            <CopyBtn text={copyText} />
        </div>
    </>

}


const PercentageCalculator: FunctionComponent<PercentageCalculatorProps> = () => {

    return <div className="flex flex-col gap-1 p-4">
        <Calculator1 />
        <div className="divider my-0"></div>
        <Calculator2 />
        <div className="divider my-0"></div>
        <Calculator3 />
    </div>
}

export default PercentageCalculator;