import dayjs from 'dayjs';
import ExchangeRate from "../types/exchange-rate";
export const EXCHANGE_RATE_KEY = "exchange-rate-v3";

export async function getExchangeRates() {
    const storedData = await chrome.storage.local.get(EXCHANGE_RATE_KEY);

    if (storedData?.[EXCHANGE_RATE_KEY]) {
        const parsedData = JSON.parse(storedData[EXCHANGE_RATE_KEY]) as ExchangeRate;
        if (parsedData.lastUpdated && dayjs().diff(parsedData.lastUpdated, 'day') < 1) {
            return parsedData;
        }
    }
    const headers = new Headers();
    // prevent caching
    headers.append('pragma', 'no-cache');
    headers.append('cache-control', 'no-cache');

    const response = await fetch(`http://api.exchangerate.host/live?base=USD&currencies=EUR,GBP&access_key=${process.env.EXCHANGERATE_HOST_ACCESS_KEY}`, {
        headers
    });
    if (response.ok) {
        const data = await response.json();
        if (!data.success) {
            const error = "Error fetching exchange rates";
            return { error };
        }
        const rates: { [key: string]: number } = {};
        Object.keys(data.quotes).forEach(key => {
            const currency = key.substring(3);
            rates[currency] = data.quotes[key];
        });
        const exchangeRate: ExchangeRate = {
            lastUpdated: dayjs().format(),
            rates
        };
        await chrome.storage.local.set({ [EXCHANGE_RATE_KEY]: JSON.stringify(exchangeRate) });
        return exchangeRate;
    } else {
        const error = "Error fetching exchange rates";
        return { error };
    }
}
