import { FunctionComponent, useState } from "react";

import cx from 'classnames';
import copy from 'copy-to-clipboard';

interface CopyBtnProps {
    text: string;
}

const CopyBtn: FunctionComponent<CopyBtnProps> = ({
    text
}) => {
    const [copied, setCopied] = useState(false);
    const copyToClipboard = () => {
        copy(text);
        setCopied(true);
        setTimeout(() => {
            setCopied(false);
        }, 1000);
    }
    return <button className={cx("btn btn-xs btn-link capitalize no-underline hover:underline font-bold", {
        "text-success": copied,
        "text-primary": !copied,
    })} disabled={!text}
        onClick={() => {
            !copied && copyToClipboard();
        }}
    >
        {copied ? 'Copied!' : 'Copy'}
    </button>
}

export default CopyBtn;