import cx from "classnames";
import { FunctionComponent, useCallback, useEffect, useMemo, useState } from "react";
import formatNumber from "../lib/format-number";
import { getExchangeRates } from "../lib/getExchangeRates";
import CloseIcon from "./closeIcon";
import CopyBtn from "./copy-btn";
import NumberInputWithLabel from "./number-input";

interface PriceCalculatorProps { }

const Btn = ({ children, onClick }: any) => {
    return <button
        type="button"
        onClick={onClick}
        className="btn btn-xs btn-link capitalize no-underline hover:underline font-bold text-primary">
        {children}
    </button>
};

enum Currency {
    EUR = 'EUR',
    GBP = 'GBP',
}

const CurrencySymbol = {
    [Currency.EUR]: '',
    [Currency.GBP]: '',
}

enum Interval {
    Monthly = 'Monthly',
    Yearly = 'Yearly',
}

const getIntervalLabel = (interval?: Interval) => {
    if (!interval) return '';
    return interval.toLowerCase();
}

const PriceCalculator: FunctionComponent<PriceCalculatorProps> = () => {
    const [price, setPrice] = useState<number>();
    const [discountPt, setDiscountPt] = useState<number>();
    const [exchangeRate, setExchangeRate] = useState<number>();
    const [exchangeRateError, setExchangeRateError] = useState<string>();
    const [currency, setCurrency] = useState<Currency>();
    const [intervalValue, setIntervalValue] = useState<Interval>();

    const showOverlay = currency || intervalValue;

    const closeOverlay = () => {
        setCurrency(undefined);
        setIntervalValue(undefined);
    }

    useEffect(() => {
        if (!currency) return;
        setExchangeRate(-1); // -1 means loading
        getExchangeRates().then((response) => {
            if ((response as { error: string }).error) {
                setExchangeRateError((response as { error: string }).error);
            } else {
                setExchangeRateError(undefined);
                setExchangeRate((response as { rates: { [key: string]: number } }).rates[currency]);
            }
        });
    }, [currency]);
    const discountedPrice = useMemo(() => {
        const discount = typeof discountPt === 'number' ? discountPt / 100 : 0;
        const _price = typeof price === 'number' ? price : 0;
        return _price * (1 - discount);
    }, [price, discountPt]);

    const savings = useMemo(() => {
        const _price = typeof price === 'number' ? price : 0;
        return _price - discountedPrice;
    }, [price, discountedPrice]);

    const getIntervalValue = useCallback((value: number) => {
        if (intervalValue === Interval.Monthly) {
            return value / 12;
        }
        if (intervalValue === Interval.Yearly) {
            return value * 12;
        }
        return value;
    }, [intervalValue]);

    const getConvertedValue = useCallback((value: number) => {
        if (exchangeRate) {
            return value * exchangeRate;
        }
        return value;
    }, [exchangeRate]);

    const copyText = useMemo(() => {
        return [
            `Price: $${formatNumber(price)} USD`,
            `Discount: ${formatNumber(discountPt, 2)}%`,
            `Discounted Price: $${formatNumber(discountedPrice, 2)} USD`,
            `Savings: $${formatNumber(savings, 2)} USD`].join('\n');

    }, [price, discountPt, discountedPrice, savings]);

    const intervalCopyText = useMemo(() => {
        return [
            `Price: $${formatNumber(getIntervalValue(price ?? 0), 2)} USD /${getIntervalLabel(intervalValue)}`,
            `Discount: ${formatNumber(discountPt, 2)}%`,
            `Discounted Price: $${formatNumber(getIntervalValue(discountedPrice), 2)} USD /${getIntervalLabel(intervalValue)}`,
            `Savings: $${formatNumber(getIntervalValue(savings), 2)} USD /${getIntervalLabel(intervalValue)}`].join('\n');
    }, [
        getIntervalValue, price, discountPt, discountedPrice, savings, intervalValue
    ]);

    const currencyCopyText = useMemo(() => {
        return [
            `Price: ${CurrencySymbol[currency ?? Currency.EUR]}${formatNumber(getConvertedValue(price ?? 0), 2)}`,
            `Discount: ${formatNumber(discountPt, 2)}%`,
            `Discounted Price: ${CurrencySymbol[currency ?? Currency.EUR]}${formatNumber(getConvertedValue(discountedPrice), 2)} ${currency ?? Currency.EUR}`,
            `Savings: ${CurrencySymbol[currency ?? Currency.EUR]}${formatNumber(getConvertedValue(savings), 2)} ${currency ?? Currency.EUR}`].join('\n');
    }, [
        getConvertedValue, price, discountPt, discountedPrice, savings, currency
    ]);




    return <div className="flex flex-col gap-1 p-4">
        <NumberInputWithLabel
            name="price"
            label="Price"
            value={price}
            onChange={setPrice}
            prefix="$"
            suffix="USD"
        />
        <NumberInputWithLabel
            name="discount"
            label="Discount"
            value={discountPt}
            onChange={setDiscountPt}
            suffix="%"
        />
        <div className="flex flex-row justify-between mt-2">
            <span className="text-1xl text-base text-gray-800 font-bold">
                Discounted Price
            </span>
            <span className={cx("text-1xl text-base font-bold", {
                "text-red-700": price && discountedPrice > price,
                "text-green-700": price && discountedPrice < price,
                "text-gray-700": price && discountedPrice === price,
            })}>
                ${formatNumber(discountedPrice, 2)}
            </span>
        </div>
        <div className="flex flex-row justify-between mt-2">
            <span className="text-1xl text-base text-gray-800 font-bold">
                Savings
            </span>
            <span className={cx("text-1xl text-base font-bold", {
                "text-red-700": price && savings < 0,
                "text-green-700": price && savings > 0,
                "text-gray-700": price && savings === 0,
            })}>
                ${formatNumber(savings, 2)}
            </span>
        </div>

        <div className="flex flex-row justify-end mt-2 -mx-2">
            <Btn onClick={() => setCurrency(Currency.EUR)}>
                EUR
            </Btn>
            <Btn
                onClick={() => setCurrency(Currency.GBP)}
            >
                GBP
            </Btn>
            <Btn
                onClick={() => setIntervalValue(Interval.Monthly)}
            >
                Monthly
            </Btn>
            <Btn
                onClick={() => setIntervalValue(Interval.Yearly)}
            >
                Yearly
            </Btn>
            <div className="flex-1 invisible"></div>
            <CopyBtn text={copyText} />
        </div>
        <div className={cx("overlay absolute bottom-0 left-0 right-0 top-0 bg-black bg-opacity-50 z-10", {
            "hidden": !showOverlay
        })}> <div className="content absolute bg-white p-4 rounded shadow bottom-0 left-0 right-0">
                <div className="flex flex-row justify-end -mx-3 -mt-3">
                    <Btn
                        onClick={closeOverlay}>
                        <CloseIcon stroke='black' />
                    </Btn>
                </div>
                {
                    intervalValue && <>
                        <div className="flex flex-row justify-between mt-2">
                            <span className="text-1xl text-base text-gray-800 font-bold">
                                Discounted Price
                            </span>
                            <span className={cx("text-1xl text-base font-bold", {
                                "text-red-700": price && discountedPrice > price,
                                "text-green-700": price && discountedPrice < price,
                                "text-gray-700": price && discountedPrice === price,
                            })}>
                                ${formatNumber(getIntervalValue(discountedPrice), 2)}<span className="text-gray-500 text-xs">&nbsp;/{getIntervalLabel(intervalValue)}</span>
                            </span>
                        </div>
                        <div className="flex flex-row justify-between mt-2">
                            <span className="text-1xl text-base text-gray-800 font-bold">
                                Savings
                            </span>
                            <span className={cx("text-1xl text-base font-bold", {
                                "text-red-700": price && savings < 0,
                                "text-green-700": price && savings > 0,
                                "text-gray-700": price && savings === 0,
                            })}>
                                ${formatNumber(getIntervalValue(savings), 2)}<span className="text-gray-500 text-xs">&nbsp;/{getIntervalLabel(intervalValue)}</span>
                            </span>
                        </div>
                        <div className="flex flex-row justify-end mt-2 -mx-2">

                            <div className="flex-1 invisible"></div>
                            <CopyBtn text={intervalCopyText} />
                        </div>
                    </>
                }
                {
                    currency && <>
                        {
                            exchangeRateError && <div className="text-red-700">
                                {exchangeRateError}
                            </div>
                        }
                        <table className="table-auto w-full text-base">
                            <thead>
                                <tr>
                                    <th className="py-1 w-26 pr-1">&nbsp;</th>
                                    <th className="py-1">USD</th>
                                    <th className="py-1">{currency}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th className="py-1 text-left pr-1">
                                        Discounted Price
                                    </th>
                                    <th className="py-1 pr-1">${formatNumber(discountedPrice, 2)}</th>
                                    <th className="py-1 pr-1">{exchangeRate === - 1 ? 'loading...' : [CurrencySymbol[currency], formatNumber(getConvertedValue(discountedPrice), 2)].join('')}</th>
                                </tr>
                                <tr>
                                    <th className="py-1 text-left pr-1">
                                        Savings
                                    </th>
                                    <th className="py-1 pr-1">${formatNumber(savings, 2)}</th>
                                    <th className="py-1 pr-1">{exchangeRate === - 1 ? 'loading...' : [CurrencySymbol[currency], formatNumber(getConvertedValue(savings), 2)].join('')}</th>
                                </tr>
                            </tbody>
                        </table>
                        <div className="flex flex-row justify-end mt-2 -mx-2">

                            <div className="flex-1 invisible"></div>
                            <CopyBtn text={currencyCopyText} />
                        </div>
                    </>
                }
            </div>
        </div>
    </div>
}

export default PriceCalculator;